:root {
    --shadow: rgba(0, 0, 0, 0.65);
    --light-brown: rgb(168, 138, 99);
    --brown: rgb(92, 74, 51);
    --white: rgb(232, 233, 221);
    --transparent: rgba(255, 255, 255, 0.1);
    --blue: rgb(79, 62, 233);
}

html {
    scroll-padding-top: 60px
}

body {
    margin-top: 60px;
    scroll-padding-top: 60px;
    background: rgb(80,80,100);
    background: linear-gradient(140deg, rgba(80,80,100,1) 0%, rgba(0,0,50,1) 50%, rgba(0,0,36,1) 100%);
}

.nav {
    flex: 1;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
}

.nav ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333333;
}

.nav li {
    float: left;
}

.nav li a {
    display: block;
    color: white;
    text-align: center;
    padding: 16px;
    text-decoration: none;
}

.nav li a:hover {
    background-color: #111111;
}



.main-container {
    background-color: white;
}

.mainitem {
    color: white;
    background: rgb(0,88,176);
    background: linear-gradient(140deg, rgba(0,88,176,1) 0%, rgba(0,0,128,1) 50%, rgba(0,0,32,1) 100%);
    border-radius: 10px;
    border: 5px solid white;
    padding: 20px;
    margin: 20px;
}

.mainitem h2 {
    text-align: center;
}

.portfoliocardholder {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.portfoliocard {
    background: rgb(0,88,176);
    background: linear-gradient(140deg, rgba(0,88,176,1) 0%, rgba(0,0,128,1) 50%, rgba(0,0,32,1) 100%);;
    color: white;
    border: 5px solid white;
    border-radius: 10px;
    width: 300px;
    padding: 10px;
    margin: 10px;
}

.portfoliocard:hover {
    border: 5px solid blue;
}

.bigportfoliocard {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgb(0,88,176);
    background: linear-gradient(140deg, rgba(0,88,176,1) 0%, rgba(0,0,128,1) 50%, rgba(0,0,32,1) 100%);;
    color: white;
    border: 5px solid white;
    border-radius: 10px;    padding: 50px;
    z-index: 1000
}

 .buttonwrapperstyle {
  position: relative;
  z-index: 1
}

 .othercontentstyle {
  position: 'relative';
  z-index: 2;
  color:rgb(232, 233, 221);
  border: 5px solid green;
  border-radius: 10px;
  background-color: red;
  padding: '10px'
}

.ffvii {
    color: white;
    background: rgb(0,88,176);
    background: linear-gradient(140deg, rgba(0,88,176,1) 0%, rgba(0,0,128,1) 50%, rgba(0,0,32,1) 100%);
    border-radius: 10px;
    border: 5px solid white;
}